
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import CancelledOrderCard from './order-card/cancelled-card/CancelledOrderCard.vue'
import { CancelledOrder } from '../../../store/data/resources/orders-list/order-types'

@Component({
  name: 'CancelledOrders',
  components: {
    CancelledOrderCard
  }
})
export default class CancelledOrders extends Vue {
  @Prop({ default: [] }) orders!: CancelledOrder[]

  get shouldShowCancelledOrders() {
    return this.numberOfCancelledOrders > 0
  }
  get numberOfCancelledOrders() {
    return this.orders.length
  }
}
