
import Vue from 'vue'
import Component from 'vue-class-component'
import { Getter } from 'vuex-class'
import { Prop } from 'vue-property-decorator'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import OrdersList from './OrdersList.vue'
import ServedOrderCard from './order-card/served-card/ServedOrderCard.vue'
import { ORDER_LIST_MODE } from '@/modules/orders/constants'
import { ServedOrder } from '../../../store/data/resources/orders-list/order-types'

@Component({
  name: 'ServedOrders',
  components: {
    OrdersList,
    ServedOrderCard
  }
})
export default class ServedOrders extends Vue {
  @Prop({ default: [] }) orders!: ServedOrder[]

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  data() {
    return { ORDER_LIST_MODE }
  }

  get shouldShowServedOrders() {
    return this.numberOfServedOrders > 0
  }
  get numberOfServedOrders() {
    return this.orders.length
  }

  get servedOrdersTitle() {
    const translationKey = this.toggles.FE_PARTNERS_IS_IN_STORE_PICKUP_ENABLED
      ? 'orders.pickedUp.title'
      : 'order.dispatched.title'
    return `${this.$t(translationKey)} (${this.numberOfServedOrders})`
  }
}
