
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import BaseOrderCard from './base/BaseOrderCard.vue'
import OrderCardHeader from './header/OrderCardHeader.vue'
import OrderCardContact from './contact/OrderCardContact.vue'
import OrderCardStore from './store/OrderCardStore.vue'
import OrderReadyButton from '@/modules/orders/components/common/OrderReadyButton.vue'
import { authentication } from '@/modules/authentication/store/namespace'
import {
  IS_MULTIVIEW_GETTER,
  GET_CURRENT_STORE_ADMIN_GETTER
} from '@/modules/authentication/store/getters'
import OrderModel, { OrderDisplayCodes } from 'models/Order'
import { orderManagement } from '@/modules/orders/store/namespace'
import {
  MARK_ACTIVE_ORDER_AS_VIEWED,
  MarkActiveOrderAsViewedPayload
} from '@/modules/orders/store'
import { StoreAction } from '@/modules/store/types/store'
import { Getter } from 'vuex-class'
import { StoreAdmin, StoreAddressId } from '@/modules/store/data/resources'
import Countdown from '@/modules/orders/components/common/Countdown.vue'
import { ORDER_LIST_MODE } from '@/modules/orders/constants'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import {
  AcceptedOrder,
  PendingOrder
} from '@/modules/store/data/resources/orders-list/order-types'
import { PaymentMethod } from '@/modules/store/data/resources/payment-method'
import { GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER } from '../../../store/getters'
import GlovoBadge from '@/components/common/ui/GlovoBadge.vue'
import ProductCount from '@/modules/orders/components/common/ProductCount.vue'

@Component({
  name: 'OrderCard',
  components: {
    Countdown,
    BaseOrderCard,
    OrderCardHeader,
    OrderCardContact,
    OrderCardStore,
    OrderReadyButton,
    GlovoBadge,
    ProductCount
  }
})
export default class OrderCard extends Vue {
  @Prop() order!: AcceptedOrder | PendingOrder
  @Prop() isBlinking!: boolean
  @Prop({ default: ORDER_LIST_MODE.ACCEPTANCE_PENDING })
  listMode!: ORDER_LIST_MODE

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @authentication.Getter(IS_MULTIVIEW_GETTER)
  isMultiView!: boolean

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin

  @orderManagement.Getter(GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER)
  isKitchenCodeStoreAddress!: (id: StoreAddressId) => boolean

  @orderManagement.Action(MARK_ACTIVE_ORDER_AS_VIEWED)
  markActiveOrderAsViewed!: StoreAction<MarkActiveOrderAsViewedPayload>

  get isPickedUpByCustomer() {
    return this.order.orderInformation.isPickedUpByCustomer
  }

  get isAccepted() {
    return this.listMode === ORDER_LIST_MODE.ACCEPTED_BY_PARTNER
  }

  get isPending() {
    return this.listMode === ORDER_LIST_MODE.ACCEPTANCE_PENDING
  }

  get isMarketplace() {
    return this.order.orderInformation.isMarketplace
  }

  get isReadyForPickup() {
    return this.listMode === ORDER_LIST_MODE.READY_FOR_PICKUP
  }

  get orderHasBeenViewed() {
    return this.order.statusDetails.isNotificationPixelViewed
  }

  get shouldShowCourierInfo() {
    return this.order.courierDetails && !this.isPickedUpByCustomer
  }

  get shouldShowStoreInfo() {
    return (
      this.isMultiView &&
      !this.toggles.FE_PARTNERS_HIDE_STORE_ADDRESS_IN_ORDER_CARD
    )
  }

  get isOrderPaymentMethodCash() {
    return this.order.paymentMethod === PaymentMethod.CASH
  }

  get hasActions() {
    return this.$slots.action
  }

  get isPendingOrderWithCollectorNearby() {
    return this.isPending && this.order.statusDetails.isCollectorNearby
  }

  get isAcceptedOrderWithCollectorNearby() {
    return this.isAccepted && this.order.statusDetails.isCollectorNearby
  }

  get isPendingOrderWithCollectorNearbyAnimated() {
    return this.isPendingOrderWithCollectorNearby && this.isBlinking
  }

  get isAcceptedOrderWithCollectorNearbyAnimated() {
    return this.isAcceptedOrderWithCollectorNearby && this.isBlinking
  }

  get showFirstOrderBadge() {
    return this.order.orderInformation.isFirstOrder
  }

  get orderCodes(): OrderDisplayCodes {
    return OrderModel.getDisplayCodes({
      code: this.order.orderInformation.code,
      pickupCode: this.order.orderInformation.pickupCode,
      storeTrackingCode: this.order.orderInformation.storeTrackingCode,
      usesKitchenCode: this.isKitchenCodeStoreAddress(this.order.storeAddressId)
    })
  }

  markOrderAsShown(isVisible: boolean) {
    if (!this.orderHasBeenViewed && isVisible) {
      this.markActiveOrderAsViewed({
        orderId: this.order.id,
        storeAdminId: (this.storeAdmin || {}).id || -1
      })
    }
  }
}
