
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { Getter } from 'vuex-class'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import {
  StoreAddressId,
  StoreAddress,
  CitiesMap,
  StoresMap
} from '@/modules/store/data/resources'
import {
  GET_STORE_ADDRESSES_GETTER,
  GET_STORES_GETTER,
  GET_CITIES_GETTER
} from '@/modules/store/data/getters'

@Component({
  name: 'OrderCardStore'
})
export default class OrderCardStore extends Vue {
  @Prop() storeAddressId!: StoreAddressId

  @Getter(GET_STORE_ADDRESSES_GETTER) storeAddresses!: Record<
    StoreAddressId,
    StoreAddress
  >
  @Getter(GET_STORES_GETTER) stores!: StoresMap
  @Getter(GET_CITIES_GETTER) cities!: CitiesMap
  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  get city() {
    return Object.values(this.cities).find(city =>
      city.storeIds.includes(this.store.id)
    )
  }

  get store() {
    return this.stores[this.storeAddresses[this.storeAddressId].parentStoreId]
  }
}
