
import Vue from 'vue'
import OrdersList from './OrdersList.vue'
import CancelledOrders from './CancelledOrders.vue'
import ServedOrders from './ServedOrders.vue'
import Placeholder from 'common/Placeholder.vue'
import Component from 'vue-class-component'
import { Watch } from 'vue-property-decorator'
import { Getter, Action } from 'vuex-class'
import User from 'models/User'
import {
  GET_ACCEPTED_ORDERS_GETTER,
  GET_PENDING_ORDERS_GETTER,
  GET_CANCELLED_ORDERS_GETTER,
  GET_SERVED_ORDERS_GETTER
} from '../../store/getters'
import { StoreAction } from '@/modules/store/types/store'
import { orderManagement } from '@/modules/orders/store/namespace'

import {
  MarkCourierNearbyAsNotifiedPayload,
  MARK_COURIER_NEARBY_AS_NOTIFIED
} from '@/modules/store/data/action-types'

import GlovoBadge from 'common/ui/GlovoBadge.vue'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import AudioService, { Sounds } from '@/services/audio/AudioService'
import {
  PendingOrder,
  AcceptedOrder,
  CancelledOrder,
  ServedOrder
} from '../../../store/data/resources/orders-list/order-types'
import { ToBeMigrated } from 'types/any'
import { DISPLAY_ORDERS } from '@/modules/store/services/display-types'
import {
  GET_IS_CHECK_IN_PENDING_GETTER,
  storeManagement
} from '@/modules/store-management/store'
import { authentication } from '@/modules/authentication/store/namespace'
import { IS_MULTIVIEW_GETTER } from '@/modules/authentication/store/getters'
import Icon from '@/components/common/Icon.vue'

@Component({
  name: 'Orders',
  components: {
    OrdersList,
    Icon,
    Placeholder,
    GlovoBadge,
    CancelledOrders,
    ServedOrders
  }
})
export default class Orders extends Vue {
  @orderManagement.Getter(GET_ACCEPTED_ORDERS_GETTER)
  acceptedOrders!: AcceptedOrder[]
  @orderManagement.Getter(GET_PENDING_ORDERS_GETTER)
  pendingOrders!: PendingOrder[]
  @orderManagement.Getter(GET_CANCELLED_ORDERS_GETTER)
  cancelledOrders!: CancelledOrder[]
  @orderManagement.Getter(GET_SERVED_ORDERS_GETTER)
  servedOrders!: ServedOrder[]

  @storeManagement.Getter(GET_IS_CHECK_IN_PENDING_GETTER)
  isPendingCheckIn!: boolean

  @authentication.Getter(IS_MULTIVIEW_GETTER) isMultiview!: boolean

  @Getter isDesktop?: boolean
  @Getter user?: User
  @Getter ordersLoading?: boolean

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @Action('displayState') displayState!: ToBeMigrated
  @Action(MARK_COURIER_NEARBY_AS_NOTIFIED)
  markCourierNearbyAsNotified!: StoreAction<MarkCourierNearbyAsNotifiedPayload>

  @Watch('acceptedOrders')
  onAcceptedOrdersChange() {
    const pendingCourierNearbyNotifications = this.acceptedOrders.filter(
      order => this.isCourierNearbyButNotNotified(order)
    )
    pendingCourierNearbyNotifications.forEach(order => {
      this.markCourierNearbyAsNotified(order.id)
    })

    if (
      pendingCourierNearbyNotifications.length &&
      !AudioService.isPlaying(Sounds.courier_nearby)
    ) {
      AudioService.stopSound(Sounds.pending)
      AudioService.playSound(Sounds.courier_nearby)
    }
  }

  activated() {
    this.displayState(DISPLAY_ORDERS)
  }

  isCourierNearbyButNotNotified(order: AcceptedOrder) {
    return (
      order.statusDetails.isCollectorNearby &&
      !order.statusDetails.hasPlayedCourierNearby
    )
  }

  get checkinPending() {
    return (
      this.isPendingCheckIn &&
      !this.isMultiview &&
      this.toggles.FE_PARTNERS_WEB_CHECK_IN_ENABLED
    )
  }
}
