
import Vue from 'vue'
import { orderManagement } from '@/modules/orders/store/namespace'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import { authentication } from '@/modules/authentication/store/namespace'
import { Getter } from 'vuex-class'
import { Prop } from 'vue-property-decorator'
import Component from 'vue-class-component'
import OrderModel from 'models/Order'
import User from 'models/User'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { OrderId } from '@/modules/store'
import { ORDER_LIST_MODE } from '../../constants'
import OrderCard from './order-card/OrderCard.vue'
import OrderCardAction from './order-card/action/OrderCardAction.vue'
import OrderCardPixel from './order-card/pixel/OrderCardPixel.vue'
import OrderReadyButton from '@/modules/orders/components/common/OrderReadyButton.vue'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { DISPATCH_ORDER, DispatchOrderPayload } from '../../store'
import { StoreAdmin } from '@/modules/store/data/resources'
import { StoreAction } from '@/modules/store/types/store'
import AudioService, { Sounds } from '@/services/audio/AudioService'
import {
  GET_PENDING_ORDERS_GETTER,
  GET_ACCEPTED_ORDERS_GETTER,
  HAS_PENDING_ORDERS_GETTER
} from '../../store/getters'
import {
  AcceptedOrder,
  PendingOrder
} from '../../../store/data/resources/orders-list/order-types'
import { ONE_SECOND_IN_MILLIS } from '@/modules/core/helpers'
import BundleInfo from '../common/BundleInfo.vue'
import { GET_ORDERS_GETTER } from '@/modules/store/data/getters'
import { OrderTypes } from '@/modules/store/data/state'
import telemetry from '@/modules/telemetry/telemetry'

const BLINKING_INTERVAL_IN_MILLIS = ONE_SECOND_IN_MILLIS

@Component({
  name: 'OrdersList',
  components: {
    OrderCard,
    OrderCardAction,
    OrderCardPixel,
    OrderReadyButton,
    BundleInfo
  }
})
export default class OrdersList extends Vue {
  @Prop({ default: [] }) orders!: AcceptedOrder[] | PendingOrder[]
  @Prop({ default: ORDER_LIST_MODE.ACCEPTANCE_PENDING }) mode!: ORDER_LIST_MODE
  @Prop({ default: false }) showConfirmationPixel!: boolean

  actionLoading: Record<OrderId, boolean> = {}
  isCardBlinking = false
  blinkingTimer: NodeJS.Timeout | null = null

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>
  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin | null
  @orderManagement.Getter(HAS_PENDING_ORDERS_GETTER) hasPendingOrders!: boolean
  @orderManagement.Getter(GET_ACCEPTED_ORDERS_GETTER)
  acceptedOrders!: AcceptedOrder[]

  @orderManagement.Getter(GET_PENDING_ORDERS_GETTER)
  pendingOrders!: PendingOrder[]

  @Getter(GET_ORDERS_GETTER)
  allOrders!: Record<OrderId, OrderTypes>

  @orderManagement.Action(DISPATCH_ORDER)
  dispatchOrder!: StoreAction<DispatchOrderPayload>

  get user() {
    return User.parse(this.storeAdmin || {})
  }

  get isPending() {
    return this.mode === ORDER_LIST_MODE.ACCEPTANCE_PENDING
  }

  get isAccepted() {
    return this.mode === ORDER_LIST_MODE.ACCEPTED_BY_PARTNER
  }

  mounted() {
    if (this.isPending || this.isAccepted) {
      this.blinkingTimer = setInterval(
        this.onBlinkInterval,
        BLINKING_INTERVAL_IN_MILLIS
      )
    }
  }

  destroyed() {
    if (this.isPending) {
      clearInterval(this.blinkingTimer!)
    }
  }

  onBlinkInterval() {
    this.isCardBlinking = !this.isCardBlinking
    if (this.isPending) {
      this.playPendingSound()
    }
  }

  playPendingSound() {
    if (
      this.shouldRingForOrders &&
      this.user.isAllowedToManageOrders() &&
      this.isCardBlinking
    ) {
      if (AudioService.isPlaying(Sounds.courier_nearby)) return
      AudioService.playSound(Sounds.pending)
    }
  }

  get hasOnePendingOrder() {
    return this.pendingOrders.length === 1
  }

  get shouldRingForOrders() {
    if (this.isNotOnOrderDetails || !this.hasOnePendingOrder) {
      return this.hasPendingOrders
    }
    const firstPendingOrder = this.pendingOrders[0] || {}
    const currentOrderInViewIsTheOnlyExistingPendingOrder =
      firstPendingOrder.id === parseInt(this.$route.params.id)
    return !currentOrderInViewIsTheOnlyExistingPendingOrder
  }

  get isNotOnOrderDetails() {
    return this.$route.name !== 'details'
  }

  get shouldShowPixel() {
    return this.showConfirmationPixel
  }

  goToOrder(order: AcceptedOrder | PendingOrder) {
    const { id, storeAddressId } = order
    // @ts-ignore
    this.$analytics.onViewOrderDetails(id, storeAddressId, this.storeAdmin?.id)
    this.$router.push({
      name: 'details',
      params: {
        id: `${order.id}`,
        storeAddressId: `${order.storeAddressId}`
      }
    })
    if (this.toggles.FE_PARTNERS_ORDER_CLICK_LOG_ENABLED) {
      telemetry.Log.info(
        'ORDER_CARD_CLICKED',
        `Order card clicked (order=${order.id}, address=${order.storeAddressId})`,
        {
          attr: {
            mode: this.mode
          }
        }
      )
    }
  }

  async onDispatchButtonClick(order: AcceptedOrder | PendingOrder) {
    this.actionLoading = { ...this.actionLoading, [order.id]: true }
    await this.dispatchOrder(order)
    this.trackDispatchToCourierEvent(order)
    this.actionLoading = { ...this.actionLoading, [order.id]: false }
  }

  trackDispatchToCourierEvent(order: AcceptedOrder | PendingOrder) {
    const { id, storeAddressId } = order
    // TS Ignore comment to be taken care of in task - https://glovoapp.atlassian.net/browse/PAR-2254
    this.isOrderPickedUpByCustomer(order)
      ? // @ts-ignore
        this.$analytics.onMarkedAsPickedUpPressed(
          id,
          storeAddressId,
          this.storeAdmin?.id
        )
      : // @ts-ignore
        this.$analytics.onDispatchedToCourierPressed(
          id,
          storeAddressId,
          this.storeAdmin?.id
        )
  }

  isBundled(order: AcceptedOrder | PendingOrder) {
    return OrderModel.isBundled(order)
  }

  bundledOrder(order: AcceptedOrder | PendingOrder) {
    if (order.orderInformation.bundledOrders) {
      return this.allOrders[order.orderInformation.bundledOrders[0]]
    }
  }

  isOrderPickedUpByCustomer(order: AcceptedOrder | PendingOrder) {
    return (
      this.toggles.FE_PARTNERS_IS_IN_STORE_PICKUP_ENABLED &&
      order.orderInformation.isPickedUpByCustomer
    )
  }

  canDispatchOrder(order: AcceptedOrder | PendingOrder) {
    return (
      this.isAccepted &&
      (order.orderInformation.isMarketplace ||
        this.isOrderPickedUpByCustomer(order))
    )
  }

  dispatchOrderButtonText(order: AcceptedOrder | PendingOrder) {
    const translationKey = this.isOrderPickedUpByCustomer(order)
      ? 'order.action.pickUp'
      : 'order.dispatched.title'
    return this.$t(translationKey)
  }

  shouldShowOrderReadyButton(order: AcceptedOrder | PendingOrder) {
    return (
      this.toggles.FE_PARTNERS_ORDER_READY_BUTTON_ENABLED &&
      this.isAccepted &&
      !this.isMarketplaceOrder(order) &&
      !this.isCustomerPickupOrder(order)
    )
  }

  isMarketplaceOrder(order: AcceptedOrder | PendingOrder) {
    return order.orderInformation.isMarketplace
  }

  isCustomerPickupOrder(order: AcceptedOrder | PendingOrder) {
    return order.orderInformation.isPickedUpByCustomer
  }
}
