
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { User } from '@/modules/store/data/resources'
import Icon from '@/components/common/Icon.vue'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import { Getter } from 'vuex-class'

export enum ContactType {
  CUSTOMER = 'CUSTOMER',
  COURIER = 'COURIER'
}

function contactTypeValidator(value: any): boolean {
  const validContactTypes = [ContactType.CUSTOMER, ContactType.COURIER]
  return (
    typeof value === 'string' &&
    validContactTypes.includes(value as ContactType)
  )
}

@Component({
  name: 'OrderCardContact',
  components: {
    Icon
  }
})
export default class OrderCardContact extends Vue {
  @Prop() user!: User
  @Prop({ validator: contactTypeValidator }) contactType!: ContactType

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  get iconName() {
    return this.contactType === ContactType.CUSTOMER
      ? 'customer-info'
      : 'courier-info'
  }
}
