
import Vue from 'vue'
import { Getter } from 'vuex-class'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { authentication } from '@/modules/authentication/store/namespace'
import { IS_MULTIVIEW_GETTER } from '@/modules/authentication/store/getters'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import BaseOrderCard from '../base/BaseOrderCard.vue'
import OrderCardHeader from '../header/OrderCardHeader.vue'
import OrderCardStore from '../store/OrderCardStore.vue'
import { minutesSince } from '@/modules/orders/helpers/time'
import { ServedOrder } from '@/modules/store/data/resources/orders-list/order-types'
import { orderManagement } from '@/modules/orders/store/namespace'
import { GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER } from '@/modules/orders/store/getters'
import { StoreAddressId } from '@/modules/store/data/resources'
import OrderModel, { OrderDisplayCodes } from 'models/Order'
import GlovoBadge from '@/components/common/ui/GlovoBadge.vue'
import ProductCount from '@/modules/orders/components/common/ProductCount.vue'

@Component({
  name: 'ServedOrderCard.vue',
  components: {
    BaseOrderCard,
    OrderCardHeader,
    OrderCardStore,
    GlovoBadge,
    ProductCount
  }
})
export default class ServedOrderCard extends Vue {
  @Prop() order!: ServedOrder
  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @authentication.Getter(IS_MULTIVIEW_GETTER)
  isMultiView!: boolean

  @orderManagement.Getter(GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER)
  isKitchenCodeStoreAddress!: (id: StoreAddressId) => boolean

  goToOrder(order: ServedOrder) {
    this.$router.push({
      name: 'details',
      params: {
        id: `${order.id}`,
        storeAddressId: `${order.storeAddressId}`
      }
    })
  }

  get minutesSinceServing() {
    return minutesSince(this.order.servingTime)
  }

  get minutesSinceServingText() {
    const translationKey = this.toggles.FE_PARTNERS_IS_IN_STORE_PICKUP_ENABLED
      ? 'order.pickup.time'
      : 'order.dispatching.time'
    return this.$tc(translationKey, this.minutesSinceServing, {
      minutesSinceServing: this.minutesSinceServing
    })
  }

  get orderCodes(): OrderDisplayCodes {
    return OrderModel.getDisplayCodes({
      code: this.order.code,
      pickupCode: this.order.pickupCode,
      usesKitchenCode: this.isKitchenCodeStoreAddress(this.order.storeAddressId)
    })
  }

  get isPickedUpByCustomer() {
    return this.order.isPickedUpByCustomer
  }
}
