
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import {
  MARK_ACTIVE_ORDER_AS_LOADED,
  MarkActiveOrderAsLoadedPayload
} from '@/modules/orders/store'
import { StoreAction } from '@/modules/store/types/store'
import { authentication } from '@/modules/authentication/store/namespace'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { StoreAdmin } from '@/modules/store/data/resources'
import { orderManagement } from '@/modules/orders/store/namespace'
import {
  AcceptedOrder,
  PendingOrder
} from '@/modules/store/data/resources/orders-list/order-types'

@Component({
  name: 'OrderCardPixel',
  components: {}
})
export default class OrderCardPixel extends Vue {
  @Prop({ required: true }) order!: AcceptedOrder | PendingOrder

  @orderManagement.Action(MARK_ACTIVE_ORDER_AS_LOADED)
  markActiveOrderAsLoaded!: StoreAction<MarkActiveOrderAsLoadedPayload>

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin

  mounted() {
    if (
      this.order &&
      this.order.statusDetails &&
      !this.order.statusDetails.isNotificationPixelLoaded
    ) {
      this.markActiveOrderAsLoaded({
        orderId: this.order.id,
        storeAdminId: (this.storeAdmin || {}).id || -1
      })
    }
  }

  render() {}
}
